* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
#root,
.App {
  height: 100vh;
}

html {
  font-size: 60.25%;
  font-family: "Nunito", sans-serif;
  font-family: "Roboto", sans-serif;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 5%;
  background-color: #134ae2;
}

header h1 {
  flex: 1 0 20rem;
  text-align: center;
  color: #ffffff;
}

header nav {
  flex: 1 0 20rem;
}

header nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7rem;
  list-style: none;
}

header nav ul li a {
  font-size: 1.5rem;
  text-decoration: none;
  color: #ffffff;
  padding: 0.3rem;
  border-radius: 0.5rem;
  transition: 0.5s;
}

header nav ul li a:hover {
  background-color: #ffffff;
  color: #134ae2;
}

.homeContainer {
  height: 95%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;
}

.homeContainer h1 {
  flex-basis: 100%;
  font-size: 5rem;
}

.homeContainer a {
  flex-basis: 100%;
  text-decoration: none;
  font-size: 2rem;
}

.homeContainer h1,
.homeContainer a {
  margin-left: 25%;
}

.initial-form {
  height: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.9rem;
}

.initial-form .submit-button {
  border: none;
  border-radius: 0.5rem;
  background-color: #134ae2;
  color: #ffffff;
  padding: 0.2rem 0.4rem;
  transition: 0.5s;
}

.initial-form .submit-button:hover {
  background-color: #ffffff;
  color: #134ae2;
}

.initial-form .submit-button:active {
  background-color: #134ae2;
  color: #ffffff;
}

.todos-container {
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  gap: 1rem;
}

.todos-container .completed {
  background-color: rgba(16, 212, 98, 0.808);
  color: #ffffff;
}

.todos-container .todos {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40rem;
  gap: 0.5rem;
  flex: 0 0 5rem;
  border: 0.2rem solid #134ae2;
  border-radius: 0.7rem;
}

.todos-container .todos button {
  border: none;
  width: 2rem;
  border-radius: 0.3rem;
}

.todos-container .todos #complete-button {
  background-color: rgba(16, 212, 98, 0.808);
  color: #ffffff;
}

.todos-container .todos #edit-button {
  background-color: #c4b000;
  color: #ffffff;
}

.todos-container .todos #delete-button {
  background-color: #c42e00;
  color: #ffffff;
}
