.homeContainer {
  height: 95%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  align-content: center;

  h1 {
    flex-basis: 100%;
    font-size: 5rem;
  }

  a {
    flex-basis: 100%;
    text-decoration: none;
    font-size: 2rem;
  }

  h1,
  a {
    margin-left: 25%;
  }
}
