header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 5%;
  background-color: $main-color;
  h1 {
    flex: 1 0 20rem;
    text-align: center;
    color: $main-text-color;
  }

  nav {
    flex: 1 0 20rem;
    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 7rem;
      list-style: none;
      li {
        a {
          font-size: 1.5rem;
          text-decoration: none;
          color: $main-text-color;
          padding: 0.3rem;
          border-radius: 0.5rem;
          transition: 0.5s;
          &:hover {
            background-color: $main-text-color;
            color: $main-color;
          }
        }
      }
    }
  }
}
